#map {
  width: 100%;
  height: calc(100vh - 140px);
  transition: all 2s ease-in-out;
}

.leaflet-popup-tip-container {
  margin: 0 !important;
  transform: translateX(-50%);
}

.leaflet-popup-content {
  margin: 0 !important;
}

.popup__item {
  &-image {
    background-size: cover !important;
    background-repeat: no-repeat !important;
    background-position: center !important;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    height: 107px;
    position: relative;
    margin-bottom: 8px;
  }

  &-action {
    border-bottom-right-radius: 5px;
    border-top-left-radius: 10px;
    color: #fff;
    position: absolute;
    left: 0;
    top: 0;
    padding: 5px 10px;
    background: #4ea79a;
  }

  &-text {
    padding: 0px 14px 6px;
  }

  &-title {
    margin-bottom: 6px;
    font-weight: 200;
  }

  &-description {
    font-size: 9px;
    font-weight: 200;
    color: #757575;
    margin-bottom: 7px;
  }

  &-price {
    display: inline-block;
    padding: 2px 6px;
    background: #ececec;
    color: #939393;
    border-radius: 5px;
    font-size: 11px;
  }
}