
header {
	padding: 5px 0;
	border-bottom: 1px solid #c1c0c0;
	position: relative;

	.container {
		display: flex;
		width: 90%;
		max-width: 1100px;
		margin: 0 auto;
		align-items: center;
		justify-content: space-between;
	}
}
.main-logo {
	display: flex;
	margin: 0;
	list-style-type: none;
	padding: 0;
	position: relative;
	
	&__link {
		display: flex;
		align-items: center;
		color: #212121;
		font-size: 14px;
		font-weight: 600;
		position: relative;
		margin-right: 10px;
	}
}

.logo {
	margin: 0 25px;
}

.nav-right {
	align-items: center;
	min-width: 170px;
	display: flex;
	justify-content: flex-end;

	h3 {
		font-size: 14px;
    margin-right: 10px;
    color: #4a4a4a;
	}
	
	.user-config {
		margin-right: 20px;
		position: relative;

		&__list {
			position: absolute;
			background: #fff;
			min-width: 250px;
			right: -5px;
			top: 125%;
			border-radius: 6px;	
			border: 1px solid #cac8c8;
			z-index: 1000;

			&::before, &::after {
				content: ''; 
				position: absolute;
				right: 10px;
				top: -20px;
				border: 10px solid transparent;
				border-bottom: 10px solid #cac8c8;
			}

			&::after {
				border-bottom: 10px solid white;
				top: -19px; 
			}
		}

		&__info {
			padding: 8px 20px 6px;
			border-bottom: 1px solid #cac8c8;
			
			h3 {
				font-size: 17px;
				color: #4e4e4e;
				margin-bottom: 3px;
			}

			span {
				font-size: 14px;
				color: #8a8a8a;
			}
		}
	}
}


.logout-wrapper {
	cursor: pointer;
    display: flex;
    align-items: center;
    padding: 8px 20px;
    font-weight: 600;
    font-size: 16px;
    color: #26776b;
	
	&:hover {
		background: #26776b;
		color: #fff;
		border-bottom-right-radius: 5px;
		border-bottom-left-radius: 5px;
		
		path {
			fill: #fff;
		}
	}
}
