.homes-list__item {
    border: 1px solid #e0e0e0;
    margin-bottom: 15px;    
    flex-basis: 30%;
    margin-right: 15px;
    flex-grow: 1;

    &-image {
        background-position: center !important;
        background-repeat: no-repeat !important;
        background-size: cover !important;
        height: 140px;
        position: relative;

        &:hover {
            .homes-list__item-view {
                opacity: 1;
            }

            .homes-list__item-wrapper {
                opacity: 0.4;
            }
        }
    }

    &-action {
        position: absolute;
        top: 0;
        left: 0;
        background: #e2e2e2;
        padding: 4px 10px;
        z-index: 2;

        span {
            font-size: 13px;
        }
    }
    
    &-action.blue {
        background: #4ea79a;
        color: #fff;
    }

    &-wrapper {
        position: absolute;
        width: 100%;
        height: 100%;
        transition: all .2s ease-in-out;
        background: #000;
        opacity: 0;
        z-index: 1;
    }

    &-view {
        position: absolute;
        transform: translateX(-50%) translateY(-50%);
        transition: all .3s ease-in-out;
        top: 50%;
        left: 50%;
        height: 30px;
        z-index: 2;
        cursor: pointer;
        width: 30px;
        background: #eee;
        border-radius: 50%;
        opacity: 0;

        .eye {
            position: absolute;
            transform: translateX(-50%) translateY(-50%);
            top: calc(50% + 2px);
            left: 50%;
        }

        &:hover path {
            fill: #4ea79a;
        }
    }

    &-text {
        padding: 15px;
    }

    &-title {
        font-size: 14px;
        font-weight: 300;
    }

    &-rating {
        margin-bottom: 10px;
    }

    &-description {
        font-size: 11px;
        font-weight: 200;
        color: #565656;
        margin-bottom: 5px;
    }

    &-bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .price-wrapper {
            display: flex;
            align-items: center;
        }

        .item-price {
            padding: 5px 7px;
            font-size: 12px;
            background: #ececec;
            color: #7b7b7b;
            border-radius: 5px;
            margin-right: 8px;
        }

        .item-basket {
            width: 34px;
            height: 34px;
            cursor: pointer;
            background: #4ea79a;
            border-radius: 50%;
            position: relative;

            &:hover {
                background: #556080;

            }

            .basket {
                position: absolute;
                transform: translateX(-50%) translateY(-50%);
                top: 50%;
                left: 50%;
                width: 20px;
                height: 19px;
            }
        }
    }
}
