.comment {
    display: flex;
    padding: 10px;
    border: 1px solid #a5a5a5;
    margin-bottom: 15px;
    border-radius: 5px;

    &-image {
        background: url(/img/man.png) center no-repeat / cover;
        min-width: 60px;
        margin-right: 15px;
        height: 60px;
        border: 1px solid #b5b5b5;
        border-radius: 5px;
    }

    p {
        font-size: 13px;
        color: #676767;
    }
}