.home-details {
    &__main {
        padding: 16px;
        border-bottom: 1px solid #dadada;
        position: relative;

        &-title {
            color: #383838;
            font-size: 18px;
            margin-bottom: 3px;
        }

        &-description {
            color: #6b6b6b;
            font-size: 15px;
        }
    }

    &__close {
        position: absolute;
        right: 14px;
        top: 14px;
        cursor: pointer;

        &:hover path {
            fill: #f44336;
        }
    }

    &__assessment {
        display: flex;
        align-items: center;
    }

    &__rating {
        padding: 14px;
        border-right: 1px solid #dadada;
    }

    &__likes {
        display: flex;
        align-items: center;
        padding: 14px;
        font-size: 13px;

        .like {
            margin-right: 5px;
        }

        span {
            position: relative;
            bottom: 2px;
        }
    }

    &__price {
        position: absolute;
        top: 0;
        left: 0;
        background: #4ea79a;
        color: #fff;
        padding: 5px;
        font-size: 14px;
        border-bottom-right-radius: 5px;
    }

    &__slider {
        position: relative;

        &-title {
            position: absolute;
            transform: translateX(-50%) translateY(-50%);
            top: 50%;
            left: 50%;
            color: #fff;
            text-shadow: 2px 2px 2px #000;
            font-size: 20px
        }

        .react-slideshow-container + div.indicators {
            position: absolute;
            bottom: 17px;
            margin: 0;
            transform: translateX(-50%);
            left: 50%;
        }

        .react-slideshow-container + div.indicators > div {
            width: 12px;
            height: 12px;
            border: 2px solid #fff;
            background: rgba(255, 255, 255, 0);
        }

        .react-slideshow-container + div.indicators > div.active {
            background: #fff;
        }

        &-image {
            background-position: center !important;
            background-repeat: no-repeat !important;
            background-size: cover !important;
            height: 300px;
        }   
    }

    &__rooms {
        display: flex;
        align-items: center;
        font-size: 13px;
        border-bottom: 1px solid #dadada;

        &-item {
            display: flex;
            align-items: center;
            padding: 14px;
            border-right: 1px solid #dadada;

            .bedrooms {
                position: relative;
                top: 2px;
            }

            span {
                margin-left: 5px;
            }
        }

        &-item:last-of-type {
            border-right: 0;
        }
    }

    &__section {
        padding: 10px 15px;
        border-bottom: 1px solid #dadada;

        &-title {
            color: #4ea79a;
            font-size: 15px;
            margin-bottom: 12px;
        }

        &-wrapper {
            display: flex;

            &.comments {
                margin-bottom: 15px;
            }

            p {
                font-size: 13px;
                width: 70%;
                color: #464646;
                margin-right: 22px;
                line-height: 1.3;
            }
        }
    }

    &__agent {
        text-align: center;
        position: relative;
        width: 30%;
        top: -22px;

        &-image {
            background: url(/img/agent.jpg) center no-repeat/cover;
            height: 100px;
            width: 100px;
            border-radius: 50%;
            border: 1px solid #c3c3c3;
            margin: 0 auto;
            margin-bottom: 8px;
        }

        &-title {
            font-size: 13px;
            color: #505050;
            margin-bottom: 8px;
        }

        &-button {
            cursor: pointer;
            outline: none;
            border-radius: 5px;
            border: 0;
            padding: 9px;
            font-size: 13px;
            white-space: nowrap;
            color: #fff;
            background: #4ea79a;

            &:hover {
                background: #858da4;
            }
        }
    }

    &__comments{

        &-add {
            width: 100%;
            border-bottom-left-radius: 5px;
            border-top-left-radius: 5px;
            border-right: 0;
            outline: none;
            background: #eee;
            border: 1px solid #aaa8a4;
            padding: 6px 10px;
        }

        &-button {
            border-bottom-right-radius: 5px;
            border-top-right-radius: 5px;
            position: relative;
            cursor: pointer;
            width: 30px;
            background: #4ea79a;
            height: 30px;
            
            &:hover {
                background: #858da4;
            }

            .add {
                transform: translateX(-50%) translateY(-50%);
                position: absolute;
                top: 50%;
                left: 50%;
            }
        }
    }
}