.dashboard {
  &-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 5%;
    background-color: #4a4a4a;
  }

  .left-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    width: auto;

    .description {
      display: flex;
      align-items: center;
      margin-right: 50px;

      h4 {
        font-size: 12px;
        font-weight: 200;
        color: #f1f1f1;
      }
    }
    
    .search {
      display: flex;

      input {
        background: #cecece;
        border: 0;
        padding: 0 7px;
        color: #272727;
        outline: none;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
      }

      &-button {
        display: flex;
        align-items: center;
        padding: 9px;
        background: #4ea79a;
        cursor: pointer;
        border: 0;
        border-bottom-right-radius: 5px;
        border-top-right-radius: 5px;

        &:hover {
          background: #858da4;
        }
      }
    }
  }

  .burger-wrapper {
    cursor: pointer;

    &:hover path {
      fill: #4ea79a;
    }
  }

  &-wrapper {
    display: flex;
    position: relative;
  }

  &-content {
    z-index: 999;
    overflow: hidden;
    overflow-y: scroll;
    width: 100%;
    max-width: 50%;
    right: -50%;
    height: 100%;
    background: #fff;
    position: absolute;
    animation: dashboard_show 1s ease-in-out;
    animation-direction: alternate;
    animation-fill-mode: forwards;

    &.hide {
      animation: dashboard_hide 2s ease-in-out;
    }
  }
}

.dashboard-content__hide {
  height: 100%;
  display: flex;
  align-items: center;
  position: absolute;
  right: 0;
  width: 8px;
  top: 0;
  background: #000;
  opacity: 0.3;
  z-index: 999;
  cursor: pointer;
  animation: button_show 1s ease-in-out;
  animation-direction: alternate;
  animation-fill-mode: forwards;
  transition: all 0.2s ease-in-out;

   &.hide {
      animation: button_hide 2s ease-in-out;
    }

  .dots {
    position: relative;
    right: 4px;
    transition: 0.3s;
  }

  &:hover {
    // right: 8px;
    width: 16px;
    opacity: 0.5;

    .dots {
      right: 0;
    }
  }
}

footer {
  text-align: center;
  padding: 14px;
  font-size: 13px;
  color: #848383;
  background: #e0e0e0;

  span {
    font-weight: 600;
  }
}

/////// ANIMATE /////////

@keyframes button_show {
  0% {
    right: 0;
  }

  100% {
    right: 50%;
  }
}

@keyframes button_hide {
  0% {
    right: 50%;
  }

  100% {
    right: 0%;
  }
}

@keyframes dashboard_show {
  0% {
    right: -50%;
  }

  100% {
    right: 0%;
  }
}

@keyframes dashboard_hide {
  0% {
    right: 0%;
  }

  100% {
    right: -50%;
  }
}

