.main-content {
    &__preview {
        background-position: center !important;
        background-repeat: no-repeat !important;
        background-size: cover !important;
        text-align: center;
        padding: 49px 30px;
        color: #fff;
        position: relative;
        overflow: hidden;

        &:hover {
            &::before {
                z-index: 30;
                opacity: 0.6;
            }

            .main-content__preview-button {
               opacity: 1;
            }
        }

        &-button {
            cursor: pointer;
            display: flex;
            align-items: center;
            background: #f44336;
            transition: .3s ease-in-out;
            padding: 8px 10px;
            border-radius: 5px;
            left: 50%;
            transform: translateY(-50%) translateX(-50%);
            position: absolute;
            top: 50%;
            opacity: 0;
            z-index: 500;

            .star {
                margin-right: 5px;
            }

            span {
                font-size: 18px;
            }
        }

        &-wrapper {
            position: relative;
            z-index: 20;
        }

        &-title {
            font-size: 20px;
            text-shadow: 2px 2px 1px #000;
            margin-bottom: 16px;
        }

        &-description {
            font-size: 15px;
            color: #eaeaea;
            font-weight: 600;
            margin-bottom: 18px;
        }

        &::before {
            content: '';
            position: absolute;
            width: 100%;
            left: 0;
            top: 0;
            height: 100%;
            opacity: 0.5;
            z-index: 2;
            background: #000;
        }
    }

    .homes {
        padding: 15px;

        &-list {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-right: -15px;

            &__not-found {
                margin: 0 auto;
                color: #b5b5b5;
                font-size: 13px;
            }
        }

        &__title {
            font-size: 13px;
            color: #26776b;
            margin-bottom: 16px;
        }

    }
}
